import React, { useState, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'

import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CInput,
  CLabel,
  CTextarea,
  CFormGroup,
  CDataTable,
  CRow,
  CSelect,
  CButton,
  CAlert,
  CPagination
} from '@coreui/react'
import config from '../../config'
import axios from 'axios'
import { Pagination , Modal} from 'antd'
const ProxyTransfer = () => {
  const history = useHistory()
  const [TransferDatas, setTransferDatas] = useState([])
  const [page, setPage] = useState(1)
  const [totalItems, setTotalItems] =  useState("")
  const [InitiateProxyModal, setInitiateProxy] = useState(false)
  const [senderEmail,setSenderEmail] = useState("")
  const [beneficiaryEmail,setBeneficiaryEmail] = useState("")
  const [amount,setAmount] = useState("")
  const [currency,setCurrency] = useState("")
  const [reason,setReason] = useState("")
  const [load, setLoad] = useState(false)
  const [msg, setMsg] = useState("")
  const [fill, setFill] = useState("")
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState(false)
  const [feeModal, setFeeModal] = useState(false)
  const [feeActualAmt, setFeeActualAmt] = useState("")
  const [fee,setFee] = useState("")
  const [totalAmt, setTotalAmt] = useState("")



  const getBadge = status => {
    switch (status) {
      case 'COMPLETED':
      return 'success'
      case 'FAILED':
      return 'secondary'
      case 'Pending':
      return 'warning'
      case 'FAILED':
      return 'danger'
      default:
      return 'primary'
    }
  }

  const getFee = () => {
   if (
    senderEmail &&
    beneficiaryEmail &&
    amount && reason && currency     ) {
    setLoad(true)
  axios.post(`${config.baseUrl}/api/v1/admin/proxy-transfer/fee`,{
    "amount" : amount,
    "currency" : currency,
  }, {
    headers: {
      Authorization: `Bearer ${sessionStorage.getItem('token')}`
    }
  })
  .then(res => {
    if (res.status === 200) {
      setFeeActualAmt(amount)
      setFee(res.data.fee)
      setLoad(false)
      setFeeModal(true)
      setInitiateProxy(false)
      setTotalAmt(res.data.fee + parseInt(amount))

    }
  })
  .catch(err => {
    setLoad(false)
    if (err.response !== undefined) {
      setMsg(err.response.data.message)
      setError(true)
      setSuccess(false)
      setTimeout(() => {
        setMsg('')
        setError(false)
      }, 2500)
    } else {
      setMsg('Connection Error')
      setError(true)
      setSuccess(false)
    }
  })
}
else{

}

}

const closeInitiateTransfer = () => {
  setInitiateProxy(false)
  setCurrency("")
  setAmount("")
  setReason("")
  setBeneficiaryEmail("")
  setSenderEmail("")
}

const getPaged = queryString => {
  axios.get(`${config.baseUrl}/api/v1/admin/transactions/proxy-transfer?${queryString}`, {
    headers: {
      Authorization: `Bearer ${sessionStorage.getItem('token')}`
    }
  })
  .then(res => {
    if (res.status === 200) {
      setTotalItems(res.data.totalPages * 10)
      setTransferDatas(
        res.data.records.map(data => ({
           reference: data.reference,
            startDate: data.createdOn ? data.createdOn.slice(0,10)  : "",
            amount:  data.currency === "NGN" ? "N" + Intl.NumberFormat("en-US").format(data.amount) : "$"  + Intl.NumberFormat("en-US").format(data.amount),
            transactionStatus: data.transactionStatus,
            senderEmail: data.sender.emailAddress,
            proxyInitiator: data.proxyInitiator,
            beneficiaryEmail:data.beneficiary.emailAddress,
            senderFee: data.currency === "NGN" ? "N" + Intl.NumberFormat("en-US").format(data.senderFee) : "$"  + Intl.NumberFormat("en-US").format(data.senderFee),
        }))
        )
    }
  })
  .catch(err => {
    if (err) {
    }
  })
}

const pagination = (page, pageSize) => {
  console.log(page)
  setPage(page)
  const queryString = `pageNumber=${page}&pageSize=10`
  getPaged(queryString)
}

const initiateProxy = () => {
  setInitiateProxy(true)

}


const initiateProxyFunc = () => {
 if (
  senderEmail &&
  beneficiaryEmail &&
  amount && reason && currency     ) {
  setLoad(true)
setFeeActualAmt(amount)
axios
.post(
  `${config.baseUrl}/api/v1/admin/proxy-transfer/initiate
  `,
  {
    "senderEmail":senderEmail,
    "beneficiaryEmail" : beneficiaryEmail,
    "amount" : amount,
    "fee" : fee,
    "currency" : currency,
    "reason" : reason
  },
  {
    headers: {
      Authorization: `Bearer ${sessionStorage.getItem('token')}`
    }
  }
  )
.then(res => {
  setLoad(false)
  if (res.status === 200) {
    setSuccess(true)
    setCurrency("")
    setAmount("")
    setReason("")
    setBeneficiaryEmail("")
    setSenderEmail("")
    setFeeModal(false)
    setMsg('Proxy Transfer Initiated Successfully')
    setTimeout(() => {
      setMsg('')
      setInitiateProxy(false)
      setSuccess(false)
    }, 2500)
    getProxyTransfers()
  }
})
.catch(err => {
  setLoad(false)
  if (err.response !== undefined) {
    setMsg(err.response.data.message)
    setError(true)
    setSuccess(false)
    setTimeout(() => {
      setMsg('')
      setError(false)
    }, 2500)
  } else {
    setMsg('Connection Error')
    setError(true)
    setSuccess(false)
  }
})
} else {
      // setError(true)
      // setMsg("All fie")
      setFill(true)
      setTimeout(() => {
        setFill(false)
      }, 2500)
    }
    
  }

  const closeFeeModal = () => {
    setFeeModal(false)
  }

  

  useEffect(() => {
    getProxyTransfers()
  }, [])

  const getProxyTransfers = () => {
    axios
    .get(
      `${config.baseUrl}/api/v1/admin/transactions/proxy-transfer?pageNumber=1&pageSize=10`,
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      if (res.status === 200) {

        setTransferDatas(
          res.data.records.map(data => ({
            reference: data.reference,
            startDate: data.createdOn ? data.createdOn.slice(0,10)  : "",
            amount:  data.currency === "NGN" ? "N" + Intl.NumberFormat("en-US").format(data.amount) : "$"  + Intl.NumberFormat("en-US").format(data.amount),
            transactionStatus: data.transactionStatus,
            senderEmail: data.sender.emailAddress,
            proxyInitiator: data.proxyInitiator,
            beneficiaryEmail:data.beneficiary.emailAddress,
            senderFee: data.currency === "NGN" ? "N" + Intl.NumberFormat("en-US").format(data.senderFee) : "$"  + Intl.NumberFormat("en-US").format(data.senderFee),
          }))
          )
      }
    })
    .catch(err => {
      if (err) {
      }
    })
  }

  return (
    <CRow>
   
    <CCol>
    {success && <CAlert color='success'>{msg}</CAlert>}

        {error && <CAlert color='danger'>{msg}</CAlert>}
    <CCard>
    <CCardHeader>
    <div className='d-flex justify-content-between align-items-center'>
    <div>Proxy Transfer</div>
    <button
    type='button'
    class='btn btn-primary mr-2'
    onClick={initiateProxy}
    >
    Initiate Transfer
    </button>
    </div>
    </CCardHeader>          <CCardBody>
    <CDataTable
    items={TransferDatas}
    fields={[
      { key: 'reference', name: 'Reference' },
      { key: 'startDate', name: ' Date' },
            { key: 'senderEmail', name: 'Sender Email' },

      { key: 'beneficiaryEmail', name: 'Beneficiary Email' },
      { key: 'amount', name: 'Amount' },
      { key: 'senderFee', name: 'Sender Fee' },
      { key: 'proxyInitiator', name: 'Proxy Initiator' },
      { key: 'transactionStatus', name: 'Transaction Status' },
      ]}
      hover
      striped
      itemsPerPage={5}
      activePage={page}
      scopedSlots={{
        transactionStatus: item => (
          <td>
          <CBadge color={getBadge(item.transactionStatus)}>
          {item.transactionStatus}
          </CBadge>
          </td>
          )
      }}
      clickableRows
      />
      <div className='text-center pagination-part'>
      <Pagination
      current={page}
      total={totalItems}
      showTotal={(total, range) => `${range[0]}-${range[1]} of ${total} items`}
      defaultPageSize={10}
      onChange={pagination}
      />
      </div>
      </CCardBody>
      </CCard>
      </CCol>

      <Modal
      title={
        "Initiate Transfer"
      }
      visible={InitiateProxyModal}
      footer={null}
      maskClosable={false}
      onCancel={closeInitiateTransfer}
      >
      <>
      <CFormGroup>
      <CLabel htmlFor='name'>Currency
      </CLabel>
      <CSelect
      custom
      name='ccmonth'
      id='ccmonth'
      onChange={e => setCurrency(e.target.value)}
      value={currency}
      >
      <option selected>Select</option>
      <option selected>NGN</option>
      <option selected>USD</option>



      </CSelect>{' '}
      </CFormGroup>
      <CFormGroup>
      <CLabel htmlFor='name'>Sender Email</CLabel>
      
      <CInput
      id='name'
      type='text'
      required
      onChange={e => setSenderEmail(e.target.value)}
      value={senderEmail}
      />
      </CFormGroup>
      <CFormGroup>
      <CLabel htmlFor='name'>Beneficiary Email</CLabel>
      <CInput
      id='name'
      type='text'
      required
      onChange={e => setBeneficiaryEmail(e.target.value)}
      value={beneficiaryEmail}
      />
      </CFormGroup>
      <CFormGroup>
      <CLabel htmlFor='name'>Amount</CLabel>
      <CInput
      id='name'
      type='number'
      required
      onChange={e => setAmount(e.target.value)}
      value={amount}
      />
      </CFormGroup>
      <CFormGroup>
      <CLabel htmlFor='name'>
      Reason
      </CLabel>
      <CTextarea
      id='name'
      type='number'
      required
      onChange={e => setReason(e.target.value)}
      value={reason}
      />
      </CFormGroup>
      <div className='d-flex justify-content-end'>
      <button
      type='button'
      class='btn btn-primary mr-2'
      onClick={getFee}
      >
      {load ? (
        <div
        class='spinner-border'
        role='status'
        style={{ width: '1rem', height: '1rem' }}
        >
        <span class='sr-only'>Loading...</span>
        </div>
        ) : (
        'Submit'
        )}
        </button>
        </div>


        </>



        </Modal>


        <Modal
        title={
          "Fee Info"
        }
        visible={feeModal}
        footer={null}
        maskClosable={false}
        onCancel={closeFeeModal}
        >
        <h4>Fee Info</h4>
        <div className="d-flex justify-content-between align-items-center">
        <div>Amount</div>
        <div>{feeActualAmt}</div>
        </div>
        <br />
        <div className="d-flex justify-content-between align-items-center">
        <div>Fee</div>
        <div>{fee}</div>
        </div>
        <br />
        <div className="d-flex justify-content-between align-items-center">
        <div>Total</div>
        <div> {currency === 'USD' ? '$' : 'â‚¦'}
        {Intl.NumberFormat('en-US').format(totalAmt)}</div>
        </div>
        <br />
        <div className='d-flex justify-content-end'>
        <button
        type='button'
        class='btn btn-primary mr-2'
        onClick={initiateProxyFunc}
        >
        {load ? (
          <div
          class='spinner-border'
          role='status'
          style={{ width: '1rem', height: '1rem' }}
          >
          <span class='sr-only'>Loading...</span>
          </div>
          ) : (
          'Submit'
          )}
          </button>
          </div>
          </Modal>
          </CRow>
          )
}

export default ProxyTransfer
